import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireAuth } from '@angular/fire/auth';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

export interface UserData {
  id: string;
  name: string;
  role: string;
  color: string;
}

export interface AdvData {
  id: string;
  name: string;
  role: string;
  color: string;
}

export interface CategoryData {
  name: string;
  color: string;
}

export interface PlaceData {
  name: string;
  color: string;
}

@Component({
  selector: 'app-adminboard',
  templateUrl: './adminboard.component.html',
  styleUrls: ['./adminboard.component.css']
})


export class AdminboardComponent implements OnInit {
  modalRef: BsModalRef;

  adverts = [];
  advertsval = {};

  users = [];
  usersval = {};

  categories = [];
  categoriesval = {};

  userId: any;
  email: any;

  database: any;
  password: string;
  name : string;
  number : string;
  role : number;
  rolename : string;
  place : string;
  mask = ['+','7',' ','(',/[1-9]/, /\d/, /\d/,')' ,' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-',/\d/, /\d/];
  thisname: string;
  thisnumber: string;

  openedacc:string;

  alladverts = {};
  countadverts = {};

  allaccounts: Number;
  allcategory ={};

  objectKeys = Object.keys;

  displayedColumns: string[] = ['id', 'name', 'role', 'color'];
  dataSource: MatTableDataSource<UserData>;

  displayedColumns2: string[] = ['id', 'name', 'role', 'color'];
  dataSource2: MatTableDataSource<AdvData>;

  displayedColumns3: string[] = ['name', 'color'];
  dataSource3: MatTableDataSource<CategoryData>;

  displayedColumns4: string[] = ['name', 'color'];
  dataSource4: MatTableDataSource<PlaceData>;


  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  @ViewChild(MatPaginator, {static: true}) paginator2: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort2: MatSort;

  @ViewChild(MatPaginator, {static: true}) paginator3: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort3: MatSort;

  @ViewChild(MatPaginator, {static: true}) paginator4: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort4: MatSort;

  resultsLength3: any;

  constructor(public db: AngularFireDatabase, public auth: AngularFireAuth, private modalService: BsModalService) { 
    this.getUsers();
    this.getAdverts();
    this.getCategories();
    this.dataSource = new MatTableDataSource(this.users);
    this.dataSource2 = new MatTableDataSource(this.adverts);
    this.dataSource3 = new MatTableDataSource(this.categories);
    this.dataSource4 = new MatTableDataSource(this.allplaces);

    this.auth.authState.subscribe(user => {
      if(user) this.userId = user.uid
      if (user) this.email = user.email
     
      this.getAccount(this.userId);
    }) 

  }

  openModal(template: TemplateRef<any>,id) {
    this.modalRef = this.modalService.show(template);
    this.openedacc = id;
  }

  getCountAdverts(){
    this.db.object('adverts').valueChanges().subscribe(val => {
    
     this.countadverts = val;

     for (let i of this.objectKeys(this.countadverts)){
      if (this.countadverts[i]['status']!=1){
        delete this.countadverts[i];
      }
     }

   });
  }

  getAdverts(){
    this.db.object('adverts').valueChanges().subscribe(val => {
    
     this.alladverts = val; //  

     for (let i of this.objectKeys(this.alladverts)){
      if (this.alladverts[i]['status'] == 0) {this.alladverts[i]['status'] = ''} else if (this.alladverts[i]['status']==1){this.alladverts[i]['status']='.'} else {this.alladverts[i]['status'] = '.'}
    }
    let count = 0;
     for (let i of this.objectKeys(this.alladverts)){
      this.adverts[count] = {name: this.alladverts[i]['name'], id: i, role:this.alladverts[i]['status']};
      count = count + 1;
     }

     this.dataSource2 = new MatTableDataSource(this.adverts);

   });
  }

  getCategories(){
    this.db.object('category').valueChanges().subscribe(val => {
    
     this.allcategory = val; //  
     let count = 0;
     for (let i of this.objectKeys(this.allcategory)){
      this.categories[count] = {name: i};
      count = count + 1;
     }
     console.log(this.categories);
     this.dataSource3 = new MatTableDataSource(this.categories);
     
     this.dataSource3.paginator = this.paginator3;
     this.dataSource3.sort = this.sort3;

     this.resultsLength3 = count;

   });
   
  }
 
  allplacesval = {};
  allplaces = [];



  getPlaces(){
    this.db.object('place').valueChanges().subscribe(val => {
    
     this.allplacesval = val; //  
     let count = 0;
     for (let i of this.objectKeys(this.allplacesval)){
      this.allplaces[count] = {name: i};
      count = count + 1;
     }
   
     this.dataSource4 = new MatTableDataSource(this.allplaces);
     
     this.dataSource4.paginator = this.paginator4;
     this.dataSource4.sort = this.sort4;


   });
   
  }

  ngOnInit() {
    


    this.getCountAdverts();
    this.getAdverts();
    this.getCategories();
    this.getPlaces();

    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.dataSource2.paginator = this.paginator2;
    this.dataSource2.sort = this.sort2;

    this.dataSource3.paginator = this.paginator3;
    this.dataSource3.sort = this.sort3;
    
  }

  getAccount(userid){
    this.db.object('accounts/' + userid).valueChanges().subscribe(val => {
    
     this.database = val; //  
     
      this.role = this.database['role'];
      if (this.role == 0) {this.rolename = ''} else if (this.role==1){this.rolename=''} else {this.rolename = ''}
      this.number = this.database['number'];
      this.thisnumber = this.number;
      this.name = this.database['name'];
      this.thisname = this.name;
   });
   
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  applyFilter2(event: Event) {
    const filterValue2 = (event.target as HTMLInputElement).value;
    this.dataSource2.filter = filterValue2.trim().toLowerCase();

    if (this.dataSource2.paginator) {
      this.dataSource2.paginator.firstPage();
    }
  }

  applyFilter3(event: Event) {
    const filterValue3 = (event.target as HTMLInputElement).value;
    this.dataSource3.filter = filterValue3.trim().toLowerCase();

    if (this.dataSource3.paginator) {
      this.dataSource3.paginator.firstPage();
    }
  }

  applyFilter4(event: Event) {
    const filterValue4 = (event.target as HTMLInputElement).value;
    this.dataSource4.filter = filterValue4.trim().toLowerCase();

    if (this.dataSource4.paginator) {
      this.dataSource4.paginator.firstPage();
    }
  }

  getUsers(){
    this.db.object('accounts').valueChanges().subscribe(val => {
    
     this.usersval = val; //  
    for (let i of this.objectKeys(this.usersval)){
      if (this.usersval[i]['role'] == 0) {this.usersval[i]['role'] = '.'} else if (this.usersval[i]['role']==1){this.usersval[i]['role']=''} else {this.usersval[i]['role'] = '.'}
    }

     let count = 0;
     for (let i of this.objectKeys(this.usersval)){
      this.users[count] = {name: this.usersval[i]['name'], id: i, role:this.usersval[i]['role']};
      count = count + 1;
     }
     this.dataSource = new MatTableDataSource(this.users);
     this.allaccounts = this.users.length;

     this.dataSource.paginator = this.paginator;
     this.dataSource.sort = this.sort;

   });
   
  }

}
